'use client';

import { useState, useEffect, useCallback, useRef } from 'react';
import { ethers } from 'ethers';
import Link from 'next/link';
import { useParams, useRouter } from 'next/navigation';
import lotteryABIFile from '../../../abis/Lottery.json';

// --- Constants ---
const BNB_CHAIN_ID = '0x61';
const USDT_CONTRACT_ADDRESS = '0x337610d27c682E347C9cD60BD4b3b107C9d34dDd';
const API_BASE_URL = 'http://localhost:3001';
const usdtABI = [
    "function approve(address spender, uint256 amount) public returns (bool)",
    "function allowance(address owner, address spender) public view returns (uint256)",
    "function balanceOf(address account) public view returns (uint256)",
    "function transfer(address to, uint256 amount) public returns (bool)",
    "function transferFrom(address from, address to, uint256 amount) public returns (bool)",
    "function name() public view returns (string)",
    "function symbol() public view returns (string)",
    "function decimals() public view returns (uint8)"
];
const lotteryABI = lotteryABIFile.abi;

// --- Type Definitions ---
declare global { interface Window { ethereum?: any; }}

interface LogEntry {
    id: number;
    type: 'JOIN' | 'SYSTEM' | 'WINNER' | 'ERROR' | 'INFO' | 'PROCESS';
    message: string;
    timestamp: string;
}

interface LotteryRound {
    id: number; // This is now the room_round_id
    globalRoundId: number;
    status: 'OPEN' | 'DRAWING' | 'CLOSED';
    players: string[];
    players_snapshot: string | null;
    winner: string | null;
    prizePool: string | null;
    logs: LogEntry[];
    // These are added from the backend's room config
    name: string;
    ticketPrice: string;
    contractAddress: string;
}

interface RoomConfig {
    name: string;
    ticketPrice: string;
    contractAddress: string;
}

// --- Main Component ---
export default function RoomPage() {
    const params = useParams();
    const router = useRouter();
    const roomId = params.roomId as string;

    const [account, setAccount] = useState<string | null>(null);
    const [isCorrectNetwork, setIsCorrectNetwork] = useState(false);
    const [isOwner, setIsOwner] = useState(false);
    const [isProcessing, setIsProcessing] = useState(false);
    const [lotteryStatus, setLotteryStatus] = useState<LotteryRound | null>(null);
    const [roomConfig, setRoomConfig] = useState<RoomConfig | null>(null);
    const [displayedLogs, setDisplayedLogs] = useState<LogEntry[]>([]);
    const [error, setError] = useState<string | null>(null);
    const [isAnimationRunning, setIsAnimationRunning] = useState(false);
    const [hasAnimationRun, setHasAnimationRun] = useState(false);

    const prevRoundIdRef = useRef<number | null>(null);
    const logContainerRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (lotteryStatus && lotteryStatus.globalRoundId !== prevRoundIdRef.current) {
            setHasAnimationRun(false);
            prevRoundIdRef.current = lotteryStatus.globalRoundId;
        }
    }, [lotteryStatus]);

    useEffect(() => {
        if (logContainerRef.current) {
            logContainerRef.current.scrollTop = logContainerRef.current.scrollHeight;
        }
    }, [displayedLogs]);

    const addFrontendLog = useCallback((type: LogEntry['type'], message: string) => {
        const newLog: LogEntry = { id: Math.random() * -100000, type, message, timestamp: new Date().toISOString() };
        setDisplayedLogs(prev => [...prev, newLog]);
    }, []);

    const fetchStatus = useCallback(async () => {
        if (isAnimationRunning || !roomId) return;
        try {
            const response = await fetch(`${API_BASE_URL}/lottery-status/${roomId}`);
            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || 'Failed to fetch lottery status.');
            }
            const data: LotteryRound = await response.json();
            setLotteryStatus(data);

            if (!roomConfig) {
                setRoomConfig({
                    name: data.name,
                    ticketPrice: data.ticketPrice,
                    contractAddress: data.contractAddress,
                });
            }

            setDisplayedLogs(prevLogs => {
                const existingIds = new Set(prevLogs.map(l => l.id));
                let newLogs = data.logs.filter(log => !existingIds.has(log.id));
                if (data.status === 'DRAWING' || isAnimationRunning) {
                   newLogs = newLogs.filter(log => log.type !== 'WINNER');
                }
                return [...prevLogs, ...newLogs].sort((a,b) => new Date(a.timestamp).getTime() - new Date(b.timestamp).getTime());
            });
        } catch (error: any) {
            setError(error.message);
        }
    }, [isAnimationRunning, roomId, roomConfig]);

    const checkNetwork = useCallback(async () => {
        if (window.ethereum) {
            const chainId = await window.ethereum.request({ method: 'eth_chainId' });
            setIsCorrectNetwork(chainId === BNB_CHAIN_ID);
        }
    }, []);

    const checkOwner = useCallback(async (currentAccount: string) => {
        if (window.ethereum && currentAccount && isCorrectNetwork && roomConfig?.contractAddress) {
            const provider = new ethers.BrowserProvider(window.ethereum);
            const contract = new ethers.Contract(roomConfig.contractAddress, lotteryABI, provider);
            try {
                const ownerAddress = await contract.owner();
                setIsOwner(ownerAddress.toLowerCase() === currentAccount.toLowerCase());
            } catch (e) {
                console.error("Couldn't check owner status: ", e);
                setIsOwner(false);
            }
        } else {
            setIsOwner(false);
        }
    }, [isCorrectNetwork, roomConfig]);

    useEffect(() => {
        const handleAccountsChanged = (accounts: string[]) => {
            const newAccount = accounts.length > 0 ? accounts[0] : null;
            setAccount(newAccount);
        };
        if (window.ethereum) {
            window.ethereum.on('accountsChanged', handleAccountsChanged);
            window.ethereum.on('chainChanged', checkNetwork);
            (async () => {
                const accounts = await window.ethereum.request({ method: 'eth_accounts' });
                handleAccountsChanged(accounts);
                await checkNetwork();
            })();
            return () => {
                window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
                window.ethereum.removeListener('chainChanged', checkNetwork);
            };
        }
    }, [checkNetwork]);

    useEffect(() => {
        if (account && isCorrectNetwork && roomConfig) {
            checkOwner(account);
        } else {
            setIsOwner(false);
        }
    }, [account, isCorrectNetwork, roomConfig, checkOwner]);
    
    useEffect(() => {
        if (lotteryStatus?.status === 'DRAWING' && !isAnimationRunning && !hasAnimationRun) {
            setIsAnimationRunning(true);
            setHasAnimationRun(true);
            const runDrawingSequence = async () => {
                addFrontendLog('SYSTEM', 'Draw starting in 3...');
                await new Promise(res => setTimeout(res, 1000));
                addFrontendLog('SYSTEM', 'Draw starting in 2...');
                await new Promise(res => setTimeout(res, 1000));
                addFrontendLog('SYSTEM', 'Draw starting in 1...');
                await new Promise(res => setTimeout(res, 1000));
                
                const players = lotteryStatus.players_snapshot ? JSON.parse(lotteryStatus.players_snapshot) : [];
                if (players.length > 0) {
                    const animationDuration = 5000;
                    const startTime = Date.now();
                    const animationInterval = setInterval(() => {
                         if (Date.now() - startTime > animationDuration) {
                            clearInterval(animationInterval);
                            const closeRound = async () => {
                                addFrontendLog('SYSTEM', 'Verification complete. Announcing winner...');
                                const winnerLog = lotteryStatus.logs.find(l => l.type === 'WINNER');
                                if (winnerLog) setDisplayedLogs(prev => [...prev, winnerLog]);
                                else if (lotteryStatus.winner) addFrontendLog('WINNER', `üéâ Congratulations to the winner: ${lotteryStatus.winner}`);
                                await new Promise(res => setTimeout(res, 3000));
                                addFrontendLog('SYSTEM', 'This round is now closed. A new round will begin shortly.');
                                for (let i = 5; i > 0; i--) {
                                    addFrontendLog('SYSTEM', `Clearing board in ${i}...`);
                                    await new Promise(res => setTimeout(res, 1000));
                                }
                                setDisplayedLogs([]);
                                setIsAnimationRunning(false);
                            };
                            closeRound();
                        } else {
                            const randomIndex = Math.floor(Math.random() * players.length);
                            addFrontendLog('PROCESS', `Verifying: ${players[randomIndex]}`);
                        }
                    }, 10);
                } else {
                     addFrontendLog('ERROR', 'No participants in the draw.');
                     setIsAnimationRunning(false);
                }
            };
            runDrawingSequence();
        }
    }, [lotteryStatus, isAnimationRunning, hasAnimationRun, addFrontendLog]);

    useEffect(() => {
        fetchStatus();
        const interval = setInterval(() => {
            if (!isAnimationRunning) fetchStatus();
        }, 3000);
        return () => clearInterval(interval);
    }, [fetchStatus, isAnimationRunning]);

    const connectWallet = async () => {
        if (!window.ethereum) return setError('Please install MetaMask.');
        try {
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            setAccount(accounts[0]);
        } catch (error) { setError('Failed to connect wallet.'); }
    };

    const handleParticipate = async () => {
        if (!account || !isCorrectNetwork || !roomConfig) return;
        setIsProcessing(true);
        setError(null);
        try {
            const provider = new ethers.BrowserProvider(window.ethereum);
            const signer = await provider.getSigner();
            const usdtContract = new ethers.Contract(USDT_CONTRACT_ADDRESS, usdtABI, signer);
            const lotteryContract = new ethers.Contract(roomConfig.contractAddress, lotteryABI, signer);
            const participationFee = ethers.parseUnits(roomConfig.ticketPrice, 18);
            
            const balance = await usdtContract.balanceOf(account);
            if (balance < participationFee) throw new Error(`Insufficient USDT balance.`);
            
            const allowance = await usdtContract.allowance(account, roomConfig.contractAddress);
            if (allowance < participationFee) {
                const approveTx = await usdtContract.approve(roomConfig.contractAddress, ethers.MaxUint256);
                await approveTx.wait();
            }
            
            const participateTx = await lotteryContract.participate();
            await participateTx.wait();
            
            await fetch(`${API_BASE_URL}/participate/${roomId}`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ userAddress: account })
            });
            await fetchStatus();
        } catch (error: any) {
            setError(`Participation failed: ${error.reason || error.message}`);
        } finally {
            setIsProcessing(false);
        }
    };
    
    return (
        <div className="flex h-screen bg-gray-900 text-white font-sans">
            {isProcessing && (
                <div className="absolute inset-0 bg-black bg-opacity-70 flex flex-col justify-center items-center z-50">
                    <div className="w-16 h-16 border-4 border-yellow-400 border-t-transparent rounded-full animate-spin"></div>
                    <p className="mt-4 text-white text-xl font-semibold">Processing Transaction...</p>
                </div>
            )}
            <div className="w-1/3 border-r border-gray-700 flex flex-col p-4">
                 <div className="mb-4">
                    <Link href="/" className="text-blue-400 hover:underline">‚Üê Back to Lobby</Link>
                </div>
                <h1 className="text-3xl font-bold mb-4">{roomConfig?.name || 'Loading Room...'}</h1>
                <h2 className="text-xl font-semibold mb-2 text-gray-400">Global Round #{lotteryStatus?.globalRoundId || '...'}</h2>
                <div className="text-lg text-yellow-400 mb-4">
                    Prize Pool: <span className="font-bold">{parseFloat(lotteryStatus?.prizePool || '0').toFixed(4)}</span> USDT
                </div>
                <div className="mb-4">
                    {account ? (
                        <div className="flex items-center justify-between">
                            <span className="text-sm bg-green-500 rounded px-2 py-1">{`${account.substring(0, 6)}...${account.substring(account.length - 4)}`}</span>
                            {isOwner && <Link href={`/admin?roomId=${roomId}`} className="text-sm bg-red-600 rounded px-2 py-1">Admin</Link>}
                        </div>
                    ) : (
                        <button onClick={connectWallet} className="w-full bg-blue-600 p-2 rounded">Connect Wallet</button>
                    )}
                </div>
                {error && <p className="text-red-500 text-xs my-2">{error}</p>}
                <button
                    onClick={handleParticipate}
                    disabled={isProcessing || lotteryStatus?.status !== 'OPEN' || !account || !isCorrectNetwork}
                    className="w-full bg-yellow-500 text-black font-bold p-3 rounded disabled:bg-gray-600"
                >
                    {lotteryStatus?.status === 'OPEN' && roomConfig ? `Participate (${roomConfig.ticketPrice} USDT)` : `Round is ${lotteryStatus?.status || 'Loading...'}`}
                </button>
                <h2 className="text-xl font-bold mt-6 mb-2">Participants ({lotteryStatus?.players?.length || 0})</h2>
                <div className="bg-gray-800 rounded p-2 flex-grow overflow-y-auto">
                    {lotteryStatus?.players?.map((player, index) => <div key={index} className="text-sm truncate font-mono">{player}</div>)}
                </div>
            </div>
            <div className="w-2/3 flex flex-col h-full">
                <div ref={logContainerRef} className="flex-grow p-4 bg-black font-mono text-sm h-full overflow-y-auto">
                    {displayedLogs.map((log, i) => (
                        <div key={log.id || i} className={`${
                            log.type === 'WINNER' ? 'text-yellow-400 font-bold text-lg' 
                            : log.type === 'ERROR' ? 'text-red-500' 
                            : log.type === 'JOIN' ? 'text-blue-400'
                            : log.type === 'PROCESS' ? 'text-green-400'
                            : 'text-gray-400'
                        }`}>
                            <span className="text-gray-600 mr-2">{new Date(log.timestamp).toLocaleTimeString()}</span>
                            <span>{log.message}</span>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
} 